<script>
export default {
    data() {
        return {
            activeName:'0',
            input3:'',
            order_list:'',//订单列表
            imgUrl:'',//imgIP
            fileList: [],
            token:{'Authorization':'Bearer ' + sessionStorage.token},
            pageSize:7,//每页显示条数
            pageTptal:1,//总条数
            sync:1,//当前页数
        };
    },
    filters: {
        zhuangtai:function(value,i){
            if(i == 'cn'){
                if(value == -1){
                    return "未支付";
                }
                if(value == 1){
                    return "待发货";
                }
                if(value == 2){
                    return "待收货";
                }
                if(value == 3){
                    return "已完成";
                }
            }else{
                if(value == -1){
                    return "non-payment";
                }
                if(value == 1){
                    return "To send the goods";
                }
                if(value == 2){
                    return "wait for receiving";
                }
                if(value == 3){
                    return "off the stocks";
                }
            }
        },
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.getList(1);
        _this.apis.get(_this.apis.order_list,{params:{lang:_this.apis.lang,page:1,pageNumber:_this.pageSize}}).then(function(res) {
            if(res.data.code == 0 ){
                _this.order_list = res.data.data.data
                _this.pageTptal = Number(res.data.data.total)
                _this.sync = Number(res.data.data.current_page)
            }
        })
    },
    watch:{
    },
    methods:{
        handleSizeChange(){
        },
        handleCurrentChange(val) {
            var _this = this;
            _this.getList(val);
        },
        getList(val){
            var _this = this;
            if(_this.activeName == 0){
                var status = ''
            }else{
                var status = _this.activeName
            }
            _this.apis.get(_this.apis.order_list,{params:{lang:_this.apis.lang,status:status,page:val,pageNumber:_this.pageSize}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.order_list = res.data.data.data
                    _this.pageTptal = res.data.data.total
                    _this.sync = res.data.data.current_page
                }
            })
        },
        handleClick(){
            var _this = this;
            // _this.
            if(_this.activeName == 0){
                var status = ''
            }else{
                var status = _this.activeName
            }
            _this.apis.get(_this.apis.order_list,{params:{lang:_this.apis.lang,status:status,page:1,pageNumber:_this.pageSize}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.order_list = res.data.data.data
                    _this.pageTptal = res.data.data.total
                    _this.sync = res.data.data.current_page
                }
            })
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(`确定移除 ${ file.name }？`);
        },
        //下载模板
        xiazaimuban(){
            var _this = this;
        },
        //删除订单
        shanchudingdan(id){
            var _this = this;
            _this.apis.get(_this.apis.change_status,{params:{lang:_this.apis.lang,status:-2,id:id}}).then(function(res) {
                if(res.data.code == 0 ){
                    alert(res.data.msg)
                }
            })
        },
        change_status(goodId,id){
            var _this = this;
            if(id == 3){
                _this.apis.get(_this.apis.change_status,{params:{lang:_this.apis.lang,status:id,id:goodId}}).then(function(res) {
                    if(res.data.code == 0 ){
                        _this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                    }else{
                        _this.$message({
                            message: res.data.msg,
                            type: 'info'
                        });
                    }
                    _this.apis.get(_this.apis.order_list,{params:{lang:_this.apis.lang,status:status}}).then(function(res) {
                        if(res.data.code == 0 ){
                            _this.order_list = res.data.data.data
                        }
                    })
                })
            }else if(id == 1){
                _this.$router.push({
                    path:'dingdan',
                    query:{title:'订单',id:goodId}
                })
                // _this.$message({
                //     message: '开发中...',
                //     type: 'success'
                // });
            }
        },
    },
    components:{
    }
}
</script>
