            &>div:nth-child(6){
                flex: 1;
                text-align: center;
            }
        }
        .shanchu{
            line-height: 20px;
            text-align: right;
            span{
                vertical-align: middle;
                cursor:pointer;
            }
            .quanxuan{
                display: inline-block;
                width: 14px;
                height: 14px;
                margin-right: 5px;
                border: 1px solid #C7C7C7;
            }
            .shanchuIcon{
                display: inline-block;
                width: 14px;
                height: 14px;
                margin-right: 5px;
                border: 1px solid #C7C7C7;
            }
        }
        .list{
            background: #FFF2EC;
            margin: 20px 0;
            padding: 14px 0;
            padding-top: 0;
            .listTitle{
                display: flex;
                align-items: center;
                padding: 0 22px;
                border-bottom: 1px solid #e8e8e8;
                &>div{
                    height: 33px;
                    line-height: 33px;
                }
                &>div:nth-child(1){
                    line-height: 33px;
                    color: #000000;
                    font-size: 13px;
                    font-weight: bold;
                    margin-right: 15px;
                    span{
                        display: inline-block;
                        width: 14px;
                        height: 14px;
                        margin-right: 10px;
                        background: #fff;
                        border: 1px solid #C7C7C7;
                        vertical-align: middle;
                    }
                }
                &>div:nth-child(2){
                    font-size: 13px;
                    color: #000;
                }
                &>div:nth-child(3){
                    color: #FF7800;
                    flex: 1;
                    text-align: right;
                    font-size: 13px;
                }
                &>div:nth-child(4){
                    width: 115px;
                    text-align: right;
                    font-size: 13px;
                }
            }
            &>div:last-child{
                border: none;
            }
            .item{
                display: flex;
                align-items: center;
                text-align: center;
                padding: 10px 0px;
                margin: 0 16px;
                border-bottom: 1px solid #e8e8e8;
                &>div:nth-child(1){
                    width: 390px;
                    display: flex;
                    align-items: top;
                    .img{
                        width: 84px;
                        height: 84px;
                        margin-right: 16px;
                    }
                    .text{
                        flex: 1;
                        text-align: left;
                        h1{
                            font-size: 13px;
                            color: #252525;
                            -webkit-line-clamp:2;
                            -webkit-box-orient: vertical;
                            display: -webkit-box;
                            text-overflow: ellipsis;
                            overflow: hidden;
                        }
                        p{
                            margin-top: 25px;
                            font-size: 12px;
                            color: #959595;
                            -webkit-line-clamp:2;
                            -webkit-box-orient: vertical;
                            display: -webkit-box;
                            text-overflow: ellipsis;
                            overflow: hidden;
                        }
                    }
                }
                &>div:nth-child(2){
                    width: 120px;
                    &>div:nth-child(1){
                        text-decoration:line-through;
                        color: #8F8F8F;
                    }
                    &>div:nth-child(2){
                        font-size: 13px;
                        color: #000;
                    }
                }
                &>div:nth-child(3){
                    width: 100px;
                    color: #000;
                }
                &>div:nth-child(4){
                    width: 150px;
                    color: #6D6C6C;
                }
                &>div:nth-child(5){
                    width: 120px;
                    color: #000000;
                }
                &>div:nth-child(6){
                    flex: 1;
                    color: #FF8401;
                    font-size: 16px;
                    font-weight: bold;
                    text-align: right;
                    .shanchudingdan{
                        cursor:pointer;
                        display: inline-block;
                        width: 20px;
                        height: 20px;
                        margin-left: 55px;
                        background-image: url(../../../assets/img/shangcheng/shanchu.png);
                        background-size: 100% 100%;
                    }
                    .fukuanBtn{
                        color: #fff;
                        display: inline-block;
                        width: 86px;
                        height: 34px;
                        text-align: center;
                        line-height: 34px;
                        margin-right: 0;
                        background: #FFAD01;
                        border-radius: 100px;
                        cursor:pointer;
                    }
                }
            }
        }
    }
}
</style>


